.login-box{
  width:100%;
  height:100%;
  background-image: url('../../assets/images/bg.jpg');
  background-size: 100% 100%;
  .login-header{
    display: flex;
    height: 80px;
    background-color: rgba(21,20,13,0.5);
    .header-left{
      display: flex;
    align-items: center;
    img{
      width: 40px;
      height: 40px;
      margin: 0 15px 0 30px;
    }
    h1{
      font-size: 28px;
      color: #fff;
      margin-bottom: 0px;
    }
    }
    .dropdown-box{
      flex : 1;
      text-align: center;
      .dropdown{
        color: #fff;
      }
    }

  }
  .login-content{
    width: 400px;
    height: 300px;
    margin: 100px auto;
    background-color: #fff;
    padding: 20px 40px;
    h2{
      font-size: 26px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 30px;
    }
    .login-form{
      .login-form-button{
        width: 100%;
      }
    }

  }
}
@primary-color: #1DA57A;